
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import { IrouteList } from "@/api/iroute";
import {updateDriverInfo} from "@/api/driver";
export default {
props: {},
data() {
var dataset = {
IrouteOptions: [],
route: [],
route2: [],
route3: [],
route4: [],
list:[]
};
return dataset;
},
computed: {},
created() {
var dataset = this;
IrouteList().then(function(result) {
result.forEach(element => {
dataset.IrouteOptions.push({name: element.routeName,value: element.id,parent: 0});
});
});
},
mounted() {},
watch: {},
methods: {
commit()
{
this.list=[];
if(this.route.length==0){
this.$vux.toast.text('基础路线必须选择')
return;
}
this.list.push(this.route);
if(this.route2.length>0)this.list.push(this.route2[0]);
if(this.route3.length>0)this.list.push(this.route3[0]);
if(this.route4.length>0)this.list.push(this.route4[0]);
var chat=this;
var str= this.list.join(',');
updateDriverInfo("route",str).then(function(response){
console.log(response);
if(response==1)chat.$vux.toast.text("修改成功");
});
}
},
components: {
Cell,
CellBox,
Group,
PopupPicker,
XButton,
XHeader
}
};



